.sign {
  width: 330px;
  margin-right: auto;
  margin-left: auto;
  background-color: #fff;
  padding: 36px 40px;
  border-radius: 10px;
  margin-top: 64px;
  margin-bottom: 70px;
  box-shadow: 0px 24px 42px rgba(86, 52, 148, 0.1);
  .politics {
    line-height: 18px;
  }
  .wrong__code {
    display: none;
  }
  .wrong__code.show {
    display: block;
  }
  div.load {
    display: none;
    justify-content: center;
  }
  .sign__title {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: #563494;
    display: block;
    margin-bottom: 24px;
  }
  .sign__title-code {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: #563494;
    display: block;
    margin-bottom: 4px;
  }
  .sign__submit {
    background: #d118a3;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 20px;
    color: #fff;
    border: none;
    width: 100%;
    padding: 18px 0;
    border-radius: 10px;
    margin-bottom: 24px;
    cursor: pointer;
  }
  .sign__submit:disabled {
    background: #d5c4cf;
  }
  .sign__error.show {
    display: block;
  }
  .sign__error {
    display: none;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    color: #d118a3;
  }
}

form {
  > span {
    display: block;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
    color: #563494;
  }
  .client__phone {
    font-weight: bold;
  }
  .success__code {
    width: 138px;
    height: 56px;
    border: 1px solid #dfd1f3;
    box-sizing: border-box;
    border-radius: 10px;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    color: #563494;
    padding-left: 20px;
    margin-top: 24px;
  }
  .resend__text {
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 18px;
    color: #9f91b0;
    margin: 10px 0;
    span {
      margin-left: 5px;
    }
  }
}

form.hide {
  display: none;
}